<template>
	<view class="boxs">
		<view class="top">
			<view class="people">
				<view>收件人:</view>
				<text>{{order.Name||''}}</text>
				<text>{{order.Phone||''}}</text>
			</view>
			<view class="address">
				<view>地址:</view>
				<text>{{`${order.Province || ''}${order.City||''}${order.Region||''}${order.Address}`}}</text>
			</view>
		</view>
		<view class="bottom">
			<view class="bottom-left">
				<view class="round"
					:style="{backgroundColor: order.IsDefault===true?'#00efa3':'',border: order.IsDefault===true?'none':'2rpx solid #979DA6'}">
				</view>
				<view class="text" @click="updataDefault">设为默认地址</view>
			</view>
			<view class="btn">
				<view class="delect" @click="delAddress">
					<image src="@/static/img/icon_delect@2x.png" mode=""></image>
					<view class="">删除</view>
				</view>
				<view class="edital" @click="enitAddress">
					<image src="@/static/img/icon_edital@2x.png" mode=""></image>
					<view class="">编辑</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			order: {
				default: {}
			}
		},
		methods: {
			async updataDefault() {
				this.$emit('updataDefault', this.order.ID)
			},
			async delAddress() {
				this.$emit('delAddress', this.order.ID)
			},
			enitAddress() {
				let items = JSON.stringify(this.order);
				uni.setStorageSync("addressList", items);
				uni.navigateTo({
					url: `/pages/user-center/user-address/address-information/index?orderId=${this.order.ID}`
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.boxs {
		padding: 30rpx 20rpx;
		margin-bottom: 30rpx;
		background: #fff;
		border-radius: 10px;
	}

	.top {
		font-size: 28rpx;
		font-weight: 400;
		color: #333333;
		line-height: 40rpx;
		border-bottom: 2rpx solid #efefef;

		.people,
		.address {
			display: flex;
			justify-content: start;

			view:nth-of-type(1) {
				width: 110rpx;
				height: 40rpx;
			}
		}

		.people {
			padding-bottom: 20rpx;

			text:nth-of-type(1) {
				padding-right: 30rpx;
			}
		}

		.address {
			padding-bottom: 30rpx;
		}
	}

	.bottom {
		padding-top: 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.bottom-left {
			display: flex;
			justify-content: start;
			align-items: center;

			.round {
				width: 32rpx;
				height: 32rpx;
				border-radius: 50%;
				border: 2rpx solid #979DA6;
			}

			.text {
				padding-left: 10rpx;
			}
		}

		.btn {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.delect,
			.edital {
				display: flex;
				align-items: center;
			}

			.delect {
				padding-right: 30rpx;
			}

			image {
				width: 40rpx;
				height: 40rpx;
			}
		}
	}
</style>
